"use client"
import Link from 'next/link'
import React, { useEffect, useState } from 'react'
import { useRouter } from 'next/navigation'

export default function Navbar() {
  const router = useRouter()
  const [isLoggedIn, setIsLoggedIn] = useState(false)

  useEffect(() => {
    setIsLoggedIn(!!localStorage.getItem('token'))
    const onStorage = (e: StorageEvent) => {
      if (e.key === 'token') {
        setIsLoggedIn(!!e.newValue)
      }
    }
    window.addEventListener('storage', onStorage)
    return () => window.removeEventListener('storage', onStorage)
  }, [])

  const handleLogout = () => {
    localStorage.removeItem('token')
    setIsLoggedIn(false)
    router.push('/')
  }

  return (
    <nav className="sticky top-0 z-40 w-full bg-white/30 backdrop-blur-md px-6 py-3 shadow-sm">
      <div className="mx-auto flex max-w-5xl items-center justify-between">
        <Link href="/" className="font-bold text-xl text-pink-600">urbanMeet</Link>
        <div className="flex items-center gap-4">
          <Link href="/groups" className="text-sm text-zinc-700 hover:text-pink-600">Groups</Link>
          {isLoggedIn && <Link href="/dashboard" className="text-sm text-zinc-700 hover:text-pink-600">Dashboard</Link>}
          {isLoggedIn && <Link href="/profile" className="text-sm text-zinc-700 hover:text-pink-600">Profile</Link>}
          {isLoggedIn ? (
            <button onClick={handleLogout} className="rounded-md bg-zinc-200 px-3 py-1 text-sm hover:bg-zinc-300">Logout</button>
          ) : (
            <Link href="/auth/login" className="rounded-md bg-pink-500 px-3 py-1 text-sm text-white hover:bg-pink-600">Login</Link>
          )}
        </div>
      </div>
    </nav>
  )
}
